import React from 'react';

import { Link } from './common/Link';
import { Shortcut } from './Shortcut'

interface MenuProps {
  items: Link[];
  name: string;
  img: string;
  x: number;
  y: number;
}

interface MenuState {
  open: boolean;
}


export class Menu extends React.Component<MenuProps, MenuState> {
  constructor(props: MenuProps) {
    super(props);
    this.state = {open: false}
  }

  render()
  {
    const items = this.props.items;
    return (<div className="Menu">
      <div className="clickable"
          onClick={() => this.setState({open: true})}>
        <Shortcut label={this.props.name} img={this.props.img} x={this.props.x} y={this.props.y}/>
      </div>
      {this.state.open && 
      <div className="Window red">
        <div className="MenuHeader">
          <span> {this.props.name} </span>
          <div className="CancelButton right clickable" onClick={() => this.setState({open: false})}>
            x
          </div> 
        </div>
      {
        items.map( (item) => {
          return <PopUp key={item.label} link={item}>
                {item.href}
            </PopUp>;
        })
      }
      {this.props.children}
      </div>
      }
    </div>);
  }
}

interface PopUpProps {
  link: Link;
}

interface PopUpState {
  open: boolean;
}

export class PopUp extends React.Component<PopUpProps, PopUpState> {
  constructor(props: PopUpProps) {
    super(props);
    this.state = {open: false}
  }

  render() {
    const link = this.props.link;
    return (<div className = {`PopUp`}>
        <div className="clickable" onClick={() => this.setState({open: true})}>
          <Shortcut label={link.label} img={link.img} x={link.x} y={link.y}/>
        </div>
        {
          this.state.open &&
          <div className="Window green">
          <div className="MenuHeader">
            <span>
              {link.label}
            </span>
            <div className="CancelButton right clickable" onClick={() => this.setState({open: false})}> x </div>
          </div>
          <div> {this.props.children} </div>
          </div>
        }
    </div>);
  }
}
