class Gfx extends React.Component<{}> {
    render(){
        return(<div className="WriteUp">
        <div className="RightSlide">
        Scotty3D is a three-part assignment for CMU's Computer Graphics 15-462. The intent is to fully create a 3D modeling software from a very barebones framework. The final product is able to create and edit meshes, render realistic lighting with raytracing, and create animations to move meshes.
        </div>
        <div className="LeftSlide">
        The applications first part is a Mesh editor. This results in an applications capable of simple operations such as basic edge and vertex editing as well as more complex operations such as Beveling and Subdivisions.
        By combining various operations, we are able to create a very high polygon model.
        </div>
        <img src="../media/apple.png" alt="appl"/>
        <div className="centered">Roughly Edited Mesh</div>
        <img src="../media/apple-subdiv.png" alt="woo"/>
        <div className="centered">After 2 Iterations of Catmull-Clark Subdivision</div>
        <div className="RightSlide">
        The next part of the process involved implementing a full Ray-Tracing renderer. 
        The application uses a Bounding Volume Hierarchy to accelerate ray intersection calculations. 
        Light traces may involve absorption, light bounces via Monte Carlo Ray-tracing, 
        shadow ray-tracing as well as environmental lighting maps, 
        all depending on the material properties of the model and scene. 
        The renderer has support for Lambertian, Mirror and Metallic, 
        as well as Glass-like and transparent surfaces implemented through 
        Bidirectional Scattering Distribution Functions. 
        </div>
            <img src="../media/lucy_mesh.png" alt="mesh"/>
            <div className="centered"> Polygonal Mesh </div>
            <img src="../media/bvh.gif"  alt="bvh"/>
            <div className="centered"> BVH Generated </div>
            <img src="../media/lucy_render.png" alt="render"/>
            <div className="centered"> final render </div>
            <img src="../media/glass.png" alt="glass render"/>
            <div className="centered"> Glass Sphere with environmental light map </div>

        <div className="RightSlide">
        Finally, the project concludes with an simple mesh animation set of tools. Features include motion paths with cubic spline interpolation, wave functions on a mesh surface, and pill capsule bound animation rigs with inverse kinematics
        </div>
        <video controls>
            <source src="../media/animation.mp4" type="video/mp4" />
        </video>
        <div className="centered"> A simple animation showcasing the Cubic Spline Paths </div>
    </div>
        );
    }
};

class FcRc extends React.Component<{}> {
    render(){
        return(<div className="WriteUp">
            <img src="../media/face-race.png" />
            <div className="centered"> Start Screen </div>
            <div className="RightSlide">
            Face Race was a group project I worked on as the Final Project for my Computer Game Programming class. The basic premise of the game was to manipulate a character's face to allow the player react to situations and give the character proper facial reactions.
            </div>
            <div className="RightSlide">
            As the rest of the class, the game is written from scratch using C++ for engine and OpenGL/GLSL for graphics. The project also involved creating Shape Keys for the character mesh, which was done using Blender. This also involved being able to export the weights from the shapekeys using a python script, and subsequently import the weights into the application to allow for programmatic manipulation of the weights of the keys. The gameplay involves using the grab points to manipulate the shape keys and create the indicated facial expressions on the User Interface based on the prompts. 
            </div>
            <div className="RightSlide">
            Also implemented on the game is environment based lighting and a rim shader on the character, done using OpenGL and GLSL.
            </div>
            <video controls={true} src="../media/face-race.mov">
            </video>
            Video of the described gameplay
        </div>
        );
    }
};

