export class Link { 
    label: string;
    href: any;
    x: number;
    y: number;
    img: string;
    constructor (label: string, href: any, x: number, y: number, img: string) {
        this.href = href;
        this.label = label;
        this.x = x;
        this.y = y;
        this.img = img;
    }
}
 