import React from 'react';

interface ShortcutProps {
  label: string;
  img: string;
  x: number;
  y: number;
}

export class Shortcut extends React.Component<ShortcutProps> {
  public getIcon(s: string){
    switch(s) { 
      case 'folder': {
        return '/media/folder.png'
        break; 
      } 
      case 'pv': {
        return '/media/pv.png'
        break; 
      } 
      case 'mt': {
        return '/media/mt.png'
        break; 
      } 
      case 'nc': {
        return '/media/nc.png'
        break; 
      } 
      case 'me': {
        return '/media/contact.png'
        break; 
      }
      case 'abt': {
        return '/media/about.png'
        break; 
      } 
      case 'cv': {
        return '/media/cv.png'
        break; 
      } 
      default: { 
        return '/media/folder.png'
        break; 
      } 
    } 
  }

  render() {
    return <div className="Icon"
      style={{"transform": `translate(${this.props.x}vw, ${this.props.y}vh)`}}>
      <img className="IconIcon" src={this.getIcon(this.props.img)} />
      <div className="IconLabel"> {this.props.label} </div>
    </div>
  }

}
